@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Stick&display=swap);
.main__body {
  display: flex;
}
.main__header {
    display: flex;
}

.header__name {
    /* flex: 50; */
    flex-grow: 1.75;
    padding: 0px 20px;
    font-family: 'Akaya Kanadaka', cursive;
    font-size: 1.5em;
}

.icons {
     display: flex;
     /* flex: 25; */
     flex-grow: .25;
     align-items: center;
}

.search__icon {
    flex-grow: .3;
    
}
.mail__icon {
    flex-grow: .3;

}
.notify__icon {
    flex-grow: .3;

}


.candidate__details {
    display: flex;
    flex-grow: .25;
    /* flex: 25; */
}

.candidate__name {
    flex-grow: .9;
    padding: 5px;
    font-weight: bolder;
}
.candidate__photo {
    flex-grow: .1;
    padding: 5px;
    align-self: center;
}
.banner__body {
        width: 80%;
        background: #9965f4;
        margin: auto;
        border-radius: 10px;
}

.banner__content{
        width: 50%;
        padding: 30px 20px;
}

.banner__content #btn {
        background: rgb(255, 255 255);
        padding: 8px 18px;
        border-radius: 20px;
        cursor: pointer;
        font-weight: bold;
}

.banner__content #btn:hover {
        color: #ffffff;
        background-color: #fabd17;   
}
.main__recruit {
    margin: 40px 35px
}

.recruit__header {
    display: flex;
    justify-content: space-between;
}

.rec__name {
    margin: 0px 15px;
    font-family: 'Akaya Kanadaka', cursive;
    font-size: 1.8em;

}

.rec__button {
    margin: 0px 15px;
    align-self: center;
}

.rec__button #btn {
    padding: 9px 26px;
    border-radius: 20px;
    cursor: pointer;
    font-size: larger;
}
.rec__button #btn:hover {
    color: #ffffff;
    background-color: #9965f4;
}

.recruit__lists{
    display: flex;
    flex-direction: column;
}

.recruit__fields {
    padding: 0px 10px;
    display: flex;
    justify-content: space-between;
    font-size: 1.4rem;
    
}

.recruit__fields #name{
width: 33%;
text-align: center;
padding: 0px 50px;
padding-right: 0px;
}
.recruit__fields #designation{
    width: 33%;

}
.recruit__fields #status{
    width: 33%;

}


.recruit__list {

}

.list {
    display: flex;
    justify-content: space-evenly;
    padding: 20px 10px;
    margin: 20px 5px;
    border-radius: 10px;
    background: #FFFFFF;
    font-size: larger;
}

.list__picname {
    display: flex;
    justify-content: space-around;
    margin-left: 10px;
    }
 
    
.picname__name {
    align-self: center;
    margin-left: 10px;
}


.list__designation {
    align-self: center;


}
.list__status{
    align-self: center;


}
.list__more {
    align-self: center;

}
.candidate__card {
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    margin: 80px 50px ;
       border-radius: 15px;
    padding: 10px 0px;
    font-size: larger;
}

.candidate__card:hover {
    transition: transform .5s ease;
    transform: scale(1.1)
}
 
.candidate__photo {
justify-content: center;
}

.candidate__name {
    align-self: center;

}

.candidate__designation {
    align-self: center;
    margin: 15px 5px;
}
.candidate__icons{
    display: flex;
    justify-content:space-evenly;
    padding: 15px 5px;

}

.candidate__details {
    justify-content: center;

}

.tble tr{
height: 40px;
}


.calender {
    margin: 80px 50px;
}


.dashboard {
    display: flex;
    flex-direction: column;
    background: #efe5fd;
    border-top-right-radius: 10px;
}

.dashboard__content {
    display: flex;
}

.second__column {
    flex: 1 1;
}

.third__column {
    flex: .45 1;
}
.drawer {
  display: flex;
  flex-direction: column;
  background-color: #9965f4;
  border-top-left-radius: 10px;
}

.drawer__header {
  display: flex;
  margin-top: 15px;
  flex-direction: row;
}

.drawer__logo {
  margin: 10px 20px;
}

.drawer__brandname {
  font-weight: bolder;
  align-self: center;
  font-family: "Akaya Kanadaka", cursive;
  font-size: 2em;
}

.drawer__buttons a {
  display: flex;
  /* border: 2px solid red; */
  color: white;
  text-decoration: none;
  font-family: 'Stick', sans-serif;
}

.drawer__button {
  display: flex;
}

a {
  margin: 25px 50px;
  padding: 5px 10px;
  border-radius: 7px;
  font-size: 25px;
}

a:hover {
  background-color: white;
  color: #9965f4;
}

